\n.place-ships .boat-wrapper {\n   display:flex;\n   flex-direction: column;\n}\n\n.place-ships .boat {\n   display: flex;\n   width: fit-content;\n}\n\n.place-ships .boat.vertical {\n   flex-direction: column;\n   align-self: center;\n}\n\n.boat .boat-square {\n   flex: none;\n   width: 7vh;\n   height: 7vh;\n   background-color: var(--my-boats);\n   border: 4px solid var(--my-boats-border);\n   border-radius: 8px;\n}\n\n.water-dialog{\n   opacity: 0;\n   padding: 2vh 2vw;\n   min-height: 100vh;\n   color: var(--text);\n   font-size: 4rem;\n   transition: opacity 0.5s;\n   text-align: center;\n}\n\n.water-dialog.show{\n   opacity: 1;\n}\n\n.choose-game{\n   color: var(--text);\n   font-size: 4rem;\n   padding: 2vh 2vw;\n   min-height: 100vh;\n   min-width: 100vw;\n   opacity: 1;\n   text-align: center;\n}\n\n.choose-game button {\n   font-size: 2rem;\n   padding: 20px;\n   border-radius: 2.5rem;\n}\n\n@media (max-width: 768px) {\n\n   :root{\n      font-size: 12px;\n   }\n\n   header{\n      justify-content: center;\n   }\n \n   .grid-container {\n     flex-direction: column;\n     gap: 2vh;\n   }\n \n   .grid {\n     height: 40vh;\n   }\n \n   .x-coordinates {\n     width: 40vh;\n   }\n \n   .y-coordinates {\n     min-height: 40vh;\n   }\n \n   .y-coordinates p {\n     height: 4vh;\n   }\n \n   .x-coordinates p {\n     width: 4vh;\n   }\n\n   button{\n      padding: 10px;\n   }\n \n   .place-ships .dialog-header {\n     gap: 2vw;\n   }\n \n   .place-ships .wrapper {\n     flex-direction: column;\n     gap: 2vh;\n   }\n \n   .place-ships .boats-drag {\n    display: none;\n   }\n\n   #direction {\n      display: none;\n   }\n \n   .place-ships .boats-drag.vertical {\n     flex-direction: row;\n     flex-wrap: wrap;\n   }\n \n   .place-ships .boat-wrapper {\n     flex-direction: row;\n     align-items: center;\n   }\n \n   .place-ships .boat {\n     flex-direction: row;\n   }\n \n   .place-ships .boat.vertical {\n     flex-direction: column;\n   }\n \n   .boat .boat-